import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useFieldArray, useForm } from 'react-hook-form';
import styled from 'styled-components';
import axios from 'axios';
// import Tag from '../components/Tag';
import AutoCompleteForArray from '../components/AutoCompleteForArray';
// import KakaoMap from '../components/KakaoMap';
// import KakaoMapAdd from '../components/KakaoMapAdd';
import AddMap from '../components/AddMap';
import useCurrentLocation from '../utils/useCurrentLocation';
import Button from '../components/Button';

enum GenderEnum {
  Female = '',
  Male = '',
}

interface IFormInput {
  name: string;
  nickname: string;
  birth: string;
  sex: GenderEnum;
  email: string;
  phone: string;
  password: string;
  passwordRetype?: string;
  memberTags: { tagId: number; tagName: string; emoji: string }[];
  locations: string;
  lat: number;
  lon: number;
  // profile: string;
}

const SignUpContainer = styled.div`
  background-color: var(--gray);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
  h1 {
    margin-top: 50px;
  }
`;

const SignUpForm = styled.form`
  width: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  > button:last-child {
    margin-bottom: 50px;
  }

  table {
    margin: 20px 0px;
    border-spacing: 20px 30px;
    tr {
      > td:nth-child(1) {
        white-space: nowrap;
        width: 50px;
      }
      > td:nth-child(2) {
        width: 100%;
        position: relative;
      }
    }
    tr:nth-child(1),
    tr:nth-child(2),
    tr:nth-child(3),
    tr:nth-child(4),
    tr:nth-child(5),
    tr:nth-child(6),
    tr:nth-child(7),
    tr:nth-child(8) {
      input,
      select {
        padding: 5px;
        font-size: 16px;
        width: 100%;
        border: none;
        outline: 1px solid rgb(120, 120, 120);
        background-color: var(--gray);
        color: white;
        &:focus {
          outline: 1px solid rgb(170, 170, 170);
        }
        &::placeholder {
          font-style: italic;
          font-size: 14px;
        }
        &:-webkit-autofill {
          box-shadow: 0 0 0 20px var(--gray) inset;
          -webkit-text-fill-color: white;
          color: white;
        }
      }
    }
    tr:nth-child(2),
    tr:nth-child(5),
    tr:nth-child(6) {
      input {
        width: 370px;
        margin-right: 10px;
      }
    }
  }
  > button:last-child {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

const ErrorMessage = styled.span`
  color: red;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  font-size: 12px;
`;

const SignUp = () => {
  const {
    register,
    control,
    getValues,
    handleSubmit,
    formState: { errors },
  } = useForm<IFormInput>();
  const { fields, append, remove } = useFieldArray({
    control,
    name: 'memberTags',
    rules: {
      validate: {
        moreThanOneTag: (values) =>
          values.length > 0 ? true : ' 1   ',
      },
    },
  });
  const navigate = useNavigate();
  const { location: currentLocation } = useCurrentLocation();
  const [checkedNickname, setCheckedNickname] = useState(true);
  const [checkedPhone, setCheckedPhone] = useState(true);
  const [checkedEmail, setCheckedEmail] = useState(true);
  const [locationString, setLocationString] = useState('');
  const [lat, setLat] = useState(0);
  const [lon, setLon] = useState(0);

  const onSubmit = (data: IFormInput) => {
    delete data.passwordRetype;
    // console.log(data);
    axios
      .post(`${process.env.REACT_APP_API_URL}/members/signup`, {
        ...data,
        lat,
        lon,
        locations: locationString,
      })
      .then((res) => {
        console.log(res);
        // alert(res);
      })
      .catch((err) => {
        navigate('/login');
        console.log(err);
        alert(err);
        console.log(
          JSON.stringify({
            ...data,
            lat,
            lon,
            locations: locationString,
          }),
        );
      });
  };

  // console.log(watch('tags'));
  // const toggles = watch('tags', []);
  // const [disabled, setDisabled] = useState(false);
  // if (toggles.length > 3) {
  //   alert(' 3 ');
  // }
  // useEffect(() => {
  //   if (toggles.length > 2) {
  //     setDisabled(true);
  //   }
  // }, [toggles]);

  const TAG_DATA = [
    { tagId: 1, tagName: '/', emoji: '' },
    { tagId: 2, tagName: '', emoji: '' },
    { tagId: 3, tagName: '', emoji: '' },
    { tagId: 4, tagName: '', emoji: '' },
    { tagId: 5, tagName: '', emoji: '' },
    { tagId: 6, tagName: '', emoji: '' },
    { tagId: 7, tagName: '', emoji: '' },
    { tagId: 8, tagName: '/', emoji: '' },
    { tagId: 9, tagName: '/', emoji: '' },
    { tagId: 10, tagName: '', emoji: '' },
    { tagId: 11, tagName: '/', emoji: '' },
    { tagId: 12, tagName: '', emoji: '' },
    { tagId: 13, tagName: '', emoji: '' },
    { tagId: 14, tagName: '', emoji: '' },
    { tagId: 15, tagName: '', emoji: '' },
    { tagId: 16, tagName: '', emoji: '' },
    { tagId: 17, tagName: '', emoji: '' },
    { tagId: 18, tagName: '', emoji: '' },
    { tagId: 19, tagName: '/', emoji: '' },
    { tagId: 20, tagName: '/', emoji: '' },
    { tagId: 21, tagName: '/', emoji: '' },
  ];

  console.log('render');

  return (
    <SignUpContainer>
      <h1> </h1>
      <span>     !</span>
      <SignUpForm onSubmit={handleSubmit(onSubmit)}>
        <table>
          <tbody>
            <tr>
              <td>
                <label htmlFor="name"></label>
              </td>
              <td>
                <input
                  id="name"
                  {...register('name', { required: ' ' })}
                />
                <ErrorMessage>{errors?.name?.message}</ErrorMessage>
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="nickname"></label>
              </td>
              <td>
                <input
                  id="nickname"
                  {...register('nickname', {
                    required: ' ',
                  })}
                />
                <ErrorMessage>{errors?.nickname?.message}</ErrorMessage>
                <Button
                  value=" "
                  onClick={() => {
                    const { nickname } = getValues();
                    axios
                      .get(
                        `${process.env.REACT_APP_API_URL}/members/signup/check-nickname/${nickname}`,
                      )
                      .then((res) => {
                        console.log(res);
                        if (res.data === true) {
                          alert('  .');
                          setCheckedNickname(true);
                        } else {
                          alert('  .');
                          setCheckedNickname(false);
                        }
                      })
                      .catch((err) => {
                        console.log(err);
                      });
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="birth"></label>
              </td>
              <td>
                <input
                  id="birth"
                  type="date"
                  {...register('birth', { required: ' .' })}
                />
                <ErrorMessage>{errors?.birth?.message}</ErrorMessage>
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="sex"></label>
              </td>
              <td>
                <select
                  id="sex"
                  {...register('sex', { required: ' .' })}
                >
                  <option value="Female"></option>
                  <option value="Male"></option>
                </select>
                <ErrorMessage>{errors?.sex?.message}</ErrorMessage>
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="email"></label>
              </td>
              <td>
                <input
                  id="email"
                  type="email"
                  {...register('email', { required: ' .' })}
                />
                <ErrorMessage>{errors?.email?.message}</ErrorMessage>
                <Button
                  value=" "
                  onClick={() => {
                    const { email } = getValues();
                    axios
                      .get(
                        `${process.env.REACT_APP_API_URL}/members/signup/check-email/${email}`,
                      )
                      .then((res) => {
                        console.log(res);
                        if (res.data !== true) {
                          alert('  .');
                          setCheckedEmail(false);
                        } else {
                          alert('  .');
                          setCheckedEmail(true);
                        }
                      })
                      .catch((err) => {
                        console.log(err);
                      });
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="phone"> </label>
              </td>
              <td>
                <input
                  id="phone"
                  type="tel"
                  {...register('phone', {
                    required: '  .',
                    pattern: {
                      value: /^(010)-[0-9]{3,4}-[0-9]{4}$/,
                      message: '010-0000-0000  .',
                    },
                  })}
                />
                <ErrorMessage>{errors?.phone?.message}</ErrorMessage>
                <Button
                  value=" "
                  onClick={() => {
                    const { phone } = getValues();
                    axios
                      .get(
                        `${process.env.REACT_APP_API_URL}/members/signup/check-phone/${phone}`,
                      )
                      .then((res) => {
                        console.log(res);
                        if (res.data !== true) {
                          alert('  .');
                          setCheckedPhone(true);
                        } else {
                          alert('  .');
                          setCheckedPhone(false);
                        }
                      })
                      .catch((err) => {
                        console.log(err);
                      });
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="password"></label>
              </td>
              <td>
                <input
                  id="password"
                  type="password"
                  {...register('password', {
                    required: ' .',
                    minLength: {
                      value: 8,
                      message: ' 8   .',
                    },
                    maxLength: {
                      value: 16,
                      message: '16   .',
                    },
                    pattern: {
                      value: /^(?=.*\d)(?=.*[a-zA-ZS]).{8,}/,
                      message: ',   .',
                    },
                  })}
                />
                <ErrorMessage>{errors?.password?.message}</ErrorMessage>
              </td>
            </tr>
            <tr>
              <td>
                <label htmlFor="passwordRetype"> </label>
              </td>
              <td>
                <input
                  id="passwordRetype"
                  type="password"
                  {...register('passwordRetype', {
                    required: ' .',
                    validate: {
                      matchesPreviousPassword: (value) => {
                        const { password } = getValues();
                        return (
                          password === value || '  .'
                        );
                      },
                    },
                  })}
                />
                <ErrorMessage>{errors?.passwordRetype?.message}</ErrorMessage>
              </td>
            </tr>
            <tr>
              <td>
                <p></p>
              </td>
              <td>
                <div>
                  {/* {currentLocation && (
                    <KakaoMapAdd
                      latitude={currentLocation.latitude}
                      longitude={currentLocation.longitude}
                    />
                  )} */}
                  {currentLocation && (
                    <AddMap
                      latitude={currentLocation.latitude}
                      longitude={currentLocation.longitude}
                      locationString={locationString}
                      setLocationString={setLocationString}
                      setLat={setLat}
                      setLon={setLon}
                    />
                  )}
                </div>
              </td>
            </tr>
            <tr>
              <td>
                <p> </p>
              </td>
              <td>
                <AutoCompleteForArray
                  fields={fields}
                  append={append}
                  remove={remove}
                  register={register}
                  control={control}
                  data={TAG_DATA}
                  tagLength={3}
                />
              </td>
            </tr>
            {/* <tr>
            <td>
          <label htmlFor="profile"> </label>
          <td>
          <td>
          <input id="profile" type="file" {...register('profile')} />
          </td>
        </tr> */}
            <Button
              onClick={() => {}}
              value="  "
              disabled={!checkedNickname && !checkedEmail && !checkedPhone}
              type="submit"
            />
          </tbody>
        </table>
      </SignUpForm>
    </SignUpContainer>
  );
};
export default SignUp;
