const classifyingGender = (gender: string) => {
  if (gender === 'Male') {
    return { text: '', icon: 'fa-solid fa-mars' };
  }
  if (gender === 'Female') {
    return { text: '', icon: 'fa-solid fa-venus' };
  }
  return { text: '', icon: 'fa-solid fa-venus-mars' };
};

export default classifyingGender;
