/* eslint-disable no-new */
import { useEffect, useRef } from 'react';
import styled from 'styled-components';
import markerImg from '../img/placeholder.png';

const MapContainer = styled.div`
  width: 100%;
  height: 100%;
  /* display: block; */

  .customoverlay {
    span {
      padding: 10px;
      border-radius: 1rem;
      background-color: rgba(1, 1, 1, 0.2);
      border: 0.05rem solid black;
      color: black;
      font-weight: 600;
    }
  }
`;

interface KakaoMapProps {
  latitude: number;
  longitude: number;
  overlayvalue?: string;
  setValue?: any;
}

const KakaoMapClick = ({
  latitude,
  longitude,
  overlayvalue = ' ',
  setValue,
}: KakaoMapProps) => {
  const mapContainer = useRef<HTMLDivElement>(null);
  const { kakao } = window;
  const position = new kakao.maps.LatLng(latitude, longitude);
  const mapOptions = {
    center: position, //  
    level: 4, //   
  };

  useEffect(() => {
    const map = new kakao.maps.Map(mapContainer.current!, mapOptions);

    const imageSrc = markerImg;
    const imageSize = new kakao.maps.Size(53, 60);
    const imageOption = { offset: new kakao.maps.Point(27, 69) };

    const markerImage = new kakao.maps.MarkerImage(
      imageSrc,
      imageSize,
      imageOption,
    );
    // const markerPosition = new kakao.maps.LatLng(latitude, longitude);

    const marker = new kakao.maps.Marker({
      position: map.getCenter(),
      image: markerImage,
    }); //  

    //    
    const content = `
    <div class="customoverlay">
    <span>${overlayvalue}</span>
    </div>
    `;

    //   
    new kakao.maps.CustomOverlay({
      map,
      position,
      content,
    });

    //     
    marker.setMap(map);
    kakao.maps.event.addListener(map, 'click', (mouseEvent: any) => {
      //  ,   
      const latlng = mouseEvent.latLng;

      //     
      marker.setPosition(latlng);

      let message = `   ${latlng.getLat()} , `;
      message += ` ${latlng.getLng()} `;
      setValue('lat', latlng.getLat());
      setValue('lon', latlng.getLng());

      const resultDiv = document.getElementById('clickLatlng');
      if (resultDiv !== null) {
        resultDiv.innerHTML = message;
      }
    });
  }, []);

  return <MapContainer id="kakao-map" ref={mapContainer} />;
};

export default KakaoMapClick;
