import FreeDataProps from '../interfaces/FreeDataProps';
import MyFreeItem from './MyFreeItem';

const WroteFree = ({ data }: any) => (
  <div>
    {
      data.length === 0 ? (
        <div>  </div>
      ) : (
        data.map((e: FreeDataProps) => (
          <MyFreeItem
            key={e.freeId}
            title={e.freeTitle}
            quota=""
            tags={e.freeTags}
            dueDate={e.createdAt}
            id={e.freeId}
          />
        ))
      )
      // free    recruits   
    }
  </div>
);
export default WroteFree;
